li {
  list-style: none;
}

.window-position-rel {
  position: relative;
}

.modal-signup-window {
  width: 600px;
  background-color: #cf0;
  border: none;
  border-radius: 4px;
  position: absolute;
  transform: translate(50%, 50%);
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.singup-form {
  padding: 40px;
  display: flex;
}

.signup-list {
  flex-direction: column;
  justify-content: center;
  display: flex;
}

.login-form {
  padding: 40px;
  display: flex;
}

.login-list {
  flex-direction: column;
  justify-content: center;
  display: flex;
}

.button-logout {
  text-align: center;
  margin-bottom: 20px;
}

.logout {
  letter-spacing: .06em;
  color: orange;
  background-color: #188ce8;
  border: none;
  border-radius: 4px;
  margin-top: 30px;
  padding: 10px;
  font-size: 16px;
  line-height: 30px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.bg-image {
  object-fit: cover;
  max-width: 1600px;
  background-image: linear-gradient(rgba(47, 48, 58, .4), rgba(47, 48, 58, .4)), url("wallpaper-big.edd6556d.jpg");
  background-repeat: no-repeat;
  padding: 100px;
  display: block;
}

.hidden {
  opacity: 0;
  display: none;
}

.window-fixed {
  z-index: 111;
  position: fixed;
}

.modal-login {
  width: 100vw;
  height: 100vh;
  opacity: 1;
  background-color: rgba(0, 0, 0, .5);
  justify-content: center;
  align-items: center;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: relative;
  top: 0;
  left: 0;
}

.close-modal-bth {
  position: absolute;
}

.auth {
  width: 55vh;
  height: 60vh;
  background-color: #188ce8;
}

.signup-backdrop {
  width: 100vw;
  height: 100vh;
  z-index: 1;
  background-color: rgba(0, 0, 0, .5);
  justify-content: center;
  align-items: center;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

